<template>
 <div class="m-letter">
    <ul class="list">
        <li class="item" v-for="item of letters" :key="item" :ref="item" @click="handleLetterClick" @touchstart="handleTouchStart" @touchmove="handleTouchMove" @touchend="handleTouchEnd">{{item}}</li>
    </ul>
 </div>
</template>

<script type="text/ecmascript-6">
export default {
    props:{
        cities:Object
    },
    data() {
    return {
        touchStatus:false,
        startY: 0,
        timer: null
    }
    },
    components: {

    },
    methods:{
        handleLetterClick (e) {
            console.log(e);
            
            this.$emit('changeLetter', e.target.innerText)
        },
        handleTouchStart (e) {
            this.touchStatus = true
        },
        handleTouchMove (e) {
            e.preventDefault() // 微信中处理 向上滑动时整个页面跟随滚动的问题 似乎这个是关键的一句话
            if (this.touchStatus) {
                if (this.timer) {
                clearTimeout(this.timer)
                }
                this.timer = setTimeout(() => { // 节流优化
                // console.log(startY)
                // 79好像就是A的距上的距离
                const touchY = e.touches[0].clientY - 79 // 好像必须写这个
                const index = Math.floor((touchY - this.startY) / 20)
                if (index >= 0 && index <= this.letters.length) {
                    console.log(this.letters[index]);
                    
                    this.$emit('changeLetter', this.letters[index])
                }
                }, 16)
            }
        },
        handleTouchEnd () {
            this.touchStatus = false
        }
    },
    computed:{
        letters () {
        const letters = []
        for (let i in this.cities) {
            letters.push(i)
        }
        return letters
        }
    },
    mounted(){
        // this.letters()
    },
    updated () {
        this.startY = this.$refs['A'][0].offsetTop
    },
}
</script>

<style>
</style>



